@charset "UTF-8";
/*==============================================================================

kojiyamauchi.com Main CSS.

==============================================================================*/
/* Common Parts */
.displayPC {
  display: block;
}

.displaySP {
  display: none;
}
/* Common Parts End. */
body {
  font-family: "Helvetica Neue", Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
}

#pageTop {
  display: inline-block;
  width: 6.5em;
  float: right;
  cursor: pointer;
  margin: 50px 0 0;
  position: relative;
  text-align: right;
}

#pageTop:hover {
  color: #cc0000;
}

#pageTop #pageTopRocket {
  position: absolute;
}

#pageTop span.fa-rocket {
  font-size: 2em;
  margin: 0 0.25% 0 0;
  vertical-align: middle;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  position: absolute;
  top: 0;
  left: 0;
  display: block;
}

#pageTop img {
  width: 2em;
  position: absolute;
  top: 28px;
  left: 0;
  height: 0;
  opacity: 0;
}

#pageTop p {
  font-size: 1em;
  font-weight: bold;
}

#pageTop.blogFastHuntPageTop {
  margin: 50px 0 -60px;
}
/*#pageTop.webSiteCreationPageTop {
    margin: 50px 0 -50px;
}*/
::-moz-selection {
  background: RGBa(204, 0, 0, 0.1);
}

::selection {
  background: RGBa(204, 0, 0, 0.1);
}

::-moz-selection {
  background: RGBa(204, 0, 0, 0.1);
}
