/*==============================================================================

Web Site Creation Portfolio Page CSS.

==============================================================================*/
body {
  &.page-template-webPortfolio {
    #loadingWrap {
      /*display:;*/
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10000000;
      overflow: hidden;
      background: #fff;
    }

    #loading {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 150px;
      height: 100px;
    }

    #loading p {
      text-align: center;
      font-size: 1.2em;
    }

    #loading p#signGo {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }

    #loading p span.fa {
      font-size: 3em;
      margin-bottom: 5px;
    }

    #loading p span.fa-car {
      transform: scale(1) translate(0, 0);
      transition: all 2.35s cubic-bezier(0.19, 1, 0.22, 1) 0.45s;
      color: #402012;
    }

    #loading p span.fa-car.scale {
      transform: scale(1.85) translate(0, -12%);
      transition: all 2.35s cubic-bezier(0.19, 1, 0.22, 1) 0.45s;
    }

    .left {
      float: left;
      width: 30%;
    }

    .center {
      float: left;
      width: 30%;
      margin-left: 5%;
      margin-right: 5%;
    }

    .right {
      float: right;
      width: 30%;
    }

    .center img,
    .left img,
    .right img {
      width: 100%;
      vertical-align: bottom;
    }

    h3 {
      margin: 7px 0 0.25em;

      span {
        font-size: 1.15rem;
      }
    }

    p {
      font-size: 14px;
      line-height: 150%;
    }

    .myWork {
      color: #fff;
      background: #bdbdbd;
      display: inline-block;
      margin: 2px 0;
      padding: 0 7px;
      border-radius: 5px;
    }

    .clearFix {
      clear: both;
      height: 10px;
    }

    .unpublishedNoImageWrap {
      position: relative;
      overflow: hidden;
    }

    .unpublished {
      position: absolute;
      top: -100%;
      z-index: 10;
    }

    .deskTop {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 11;
    }

    #microBloodScienceIMGWrap,
    #gelatoIncIMGWrap,
    #primeMarcheIMGWrap {
      position: relative;
    }

    .microBloodScienceIMG1,
    .gelatoIncIMG1,
    .primeMarcheIMG1 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 3;
    }

    .microBloodScienceIMG2,
    .gelatoIncIMG2,
    .primeMarcheIMG2 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      opacity: 0;
    }

    #fastHuntIMGWrap {
      position: relative;
    }

    .fastHuntIMG1 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 5;
    }

    .fastHuntIMG2 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 4;
      opacity: 0;
    }

    .fastHuntIMG3 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 3;
      opacity: 0;
    }

    .fastHuntIMG4 {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      opacity: 0;
    }
  }
}
@media screen and (max-width: 960px) {
  body {
    &.page-template-webPortfolio {
      .left {
        float: none;
        width: 90%;
        margin: 20px auto 0;
      }

      .center {
        float: none;
        width: 90%;
        margin: 20px auto 0;
      }

      .right {
        float: none;
        width: 90%;
        margin: 20px auto 0;
      }
    }
  }
}
@media screen and (max-width: 413px) {
  body {
    &.page-template-webPortfolio {
      .entry-title {
        font-size: 30px;
      }
    }
  }
}
/*==============================================================================

Alternative Portfolio Page CSS.

==============================================================================*/
.page-template-ds-gallery-masonry-template-php .albums-thumbnails {
  margin: 0 auto;
}
@media screen and (max-width: 413px) {
  .albums-thumbnails img {
    width: 100%;
  }
}
/*==============================================================================

Single Post Page CSS.

==============================================================================*/
body {
  &.single-post {
    #content {
      text-align: center;
      margin: 20px 0 0;
    }

    .footer-copy {
      p {
        padding-left: 0;
        padding-right: 0;
        margin: 0 auto;
        text-align: center;
        text-indent: 16em;
      }
    }
    @media screen and (max-width: 768px) {
      .displayPC {
        display: none;
      }

      .displaySP {
        display: block;
      }
    }

    &:not(.collapsed-layout) {
      .translateTop10 {
        transform: translate(0, -10%);
      }

      .translateTop15 {
        transform: translate(0, -15%);
      }
    }
  }
}
/*==============================================================================

SmartPhone & Tablet Header & Footer LayOut CSS.

==============================================================================*/
body {
  &.collapsed-layout {
    .main-header {
      z-index: 100;
    }

    .footer-copy {
      p {
        text-indent: 0;
        text-align: center;
        margin: 0 auto;
      }
    }
  }
}
